.burger-menu {
  height: 100%;
  width: 5em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  cursor: pointer;
  border-radius: 0.5vw;
  margin-top: 0.2vw;
}

.burger-bar {
  width: 4vw;
  height: 0.5vw;
  margin: 0.5vw auto;
  background-color: #fff;
  border-radius: 0.5em;
}

.menu {
  width: 25%;
  height: 35vw;
  background-color: #20c763;
  position: absolute;
  top: 5vw;
  left: 75%;
  border-radius: 1vw;
}

.hidden {
  display: none;
}

.visible {
  display: inherit;
}

.burger-bar.clicked:nth-child(1) {
  transform: rotate(45deg) translate(1vw, 1.5vw);
  transition: ease-out 0.2s;
}

.burger-bar.clicked:nth-child(2) {
  transform: scale(0.1);
  transition: ease-out 0.2s;
}

.burger-bar.clicked:nth-child(3) {
  transform: rotate(135deg) translate(-0.6vw, 1vw);
  transition: ease-out 0.2s;
}

.burger-bar.unclicked {
  transform: rotate(0) translate(0);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: ease-out 0.2s;
}

