.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.box {
  position: relative;
  width: 30%;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: rgba(0, 165, 9, 1);
  border-radius: 1vw;
  padding: 20px;
  border: 0.2vw solid #000;
  overflow: auto;
  text-align: center;
}

.close-icon {
  content: "x";
  cursor: pointer;
  position: fixed;
  right: calc(35% - 30px);
  top: calc(100vh - 85vh - 33px);
  background: #fff;
  width: 2vw;
  height: 2vw;
  border-radius: 50%;
  line-height: 2vw;
  text-align: center;
  border: 0.2vw solid #000;
  font-size: 1.5vw;
}

h2 {
  width: 100%;
